.admin-dashboard-body-card-box{
    width: 200px;
    height: 100px;
    background-color: #ffffff;
    border-radius: 10px;
    margin: 10px auto;
}
.admin-dashboard-body-card-title{
    color: rgb(172, 172, 172);
    font-size: 12px;
    padding: 15px 0 0 15px;
}
.admin-dashboard-body-card-amount{
    color: rgb(92, 92, 92);
    font-size: 15px;
    padding: 5px 0 0 15px;
}
.admin-dashboard-body-card-interval{
    color: rgb(172, 172, 172);
    font-size: 12px;
    padding: 5px 0 0 15px;
}
.admin-dashboard-body-card-icon-box{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 25px 0 0 -10px;
}
.admin-dashboard-body-card-icon-box-total-user{
    background-color: #2DCE98;
}
.admin-dashboard-body-card-icon-box-new-user{
    background-color: #06B4B5;
}
.admin-dashboard-body-card-icon-box-total-blog{
    background-color: #7764E4;
}
.admin-dashboard-body-card-icon-box-total-guide{
    background-color: #11CDEF;
}
.admin-dashboard-body-card-icon{
    width: 25px;
    height: 25px;
    margin: 13px 0 0 13px;
}
.admin-dashboard-card-title{
    color: #8898bd;
    text-align: left;
    margin: 15px 0 0 10px;
    font-size: 10px;
    font-weight: 500;
}
.admin-dashboard-card-sub-title{
    color: #000000;
    text-align: left;
    margin: 0 0 0 10px;
    font-size: 15px;
    font-weight: 500;
}

/* Line Chart */
.admin-dashboard-chart-line-btn{
    width: 70px;
    height: 40px;
    border: 1px solid #7763E4;
    margin: 5px 10px;
    color: #7763E4;
    float: right;
}
.admin-dashboard-chart-line-btn:hover{
    color: #7763E4;
}
.admin-dashboard-chart-line-btn-active{
    border: none;
    background-color: #0DB5B6;
    color: #ffffff;
}
.admin-dashboard-chart-line-btn-active:hover{
    color: #ffffff;
}
@media screen and (max-width: 1000px){
    .admin-dashboard-body-main-title{
        font-size: 15px;
        margin: 25px 0 0 25px;
        }
        .admin-dashboard-body-main-icon{
            width: 20px;
            height: 20px;
            margin: 25px 0 0 40px;
            float: left;
        }
    .admin-dashboard-body-main-text{
        color: #ffffff;
        float: left;
        font-size: 10px;
        font-weight: 500;
        margin: 30px 10px 0 10px;
    }
}