.add-blog-body-profile-img{
    width: 50px;
    height: 50px;
    float: left;
    border-radius: 50%;
    margin: 10px 0 0 0;
}
.add-blog-body-profile-name{
    font-size: 30px;
    color: #DB5D15;
    margin: 10px 0 0 30px;
    float: left;
}
.add-blog-body-profile-info-text{
    color: #6C757D;
}
.add-blog-body-add-blog-img{
    width: 100%;
}
.add-blog-body-add-blog-title{
    font-size: 25px;
    text-align: left;
    font-weight: 500;
}
.add-blog-body-add-blog-text{
    font-size: 15px;
    text-align: left;
    font-weight: 500;
}
.add-blog-body-add-blog-category-container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.add-blog-body-add-blog-category-box{
    height: 30px;
    line-height: 26px;
    font-size: 15px;
    font-weight: 500;
    color: #DB5D15;
    border: 2px solid #DB5D15;
    border-radius: 20px;
    padding: 0 20px;
    margin: 20px;
}
.add-blog-body-add-blog-bottom-pic{
    width: 100%;
    height: 100%;
    border-radius: 5px;
}
@media screen and (max-width:767px){
    .add-blog-body-profile-img{
        width: 30px;
        height: 30px;
        margin: 20px 0 0 0;
    }
    .add-blog-body-profile-name{
        font-size: 12px;
        line-height: 50px;
    }
    .add-blog-body-profile-info-text{
        font-size: 13px;
    }
}