/* Post */
.home-post-head-profile{
    width: 35px;
    height: 35px;
    float: left;
    border-radius: 50%;
    margin: 2.5% 0 0 0;
}
.home-post-head-title{
    font-size: 20px;
    color: #DB5D15;
    line-height: 50px;
    margin: 0 0 0 50px;
}
.home-post-head-info{
    text-align: left;
    font-size: 13px;
    color: rgb(184, 184, 184);
}
.home-post-body-post-img{
    width: 100%;
    height: 100%;
}
.home-post-body-post-title{
    font-size: 15px;
    color: #000000;
    font-weight: 500;
    text-align: left
}
.home-post-body-post-text{
    font-size: 13px;
    color: #000000;
    font-weight: 500;
    text-align: left
}
.home-post-body-post-btn{
    background-color: #DB5D15;
    height: 30px;
    border-radius: 20px;
    line-height: 10px;
    color: #ffffff;
}
.home-post-body-post-btn:hover{
    color: #ffffff;
}
.home-post-body-post-btn:focus{
    box-shadow: none;
}

/* Mail */
.home-mail-bg{
    background-color: #DB5D15;
    border-radius: 10px;
    cursor: pointer;
}
.home-mail-title{
    text-align: center;
    font-size: 20px;
    color: #ffffff;
}
.home-mail-icon{
    width: 50%;
}

/* Tag */
.home-tag-bg{
    background-color: #ffffff;
    border-radius: 10px;
    height: 100%;
    /* cursor: pointer; */
}
.home-tag-title{
    color: black;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 20px;
}
.home-tag-container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.home-tag-box{
    height: 30px;
    border: 2px solid #DB5D15;
    color: #DB5D15;
    font-size: 13px;
    font-weight: 500;
    line-height: 26px;
    padding: 0 10px;
    border-radius: 20px;
    margin: 5px;
}

/* Post Card */
.home-card-box{
    height: 370px;
    width: 300px;
    border-radius: 10px;
    background-color: #ffffff;
    /* margin: auto; */
    margin: 10px auto;
}
.home-card-img{
    width: 100%;
    height: 175px;
    border-radius: 10px;
}
.home-card-author{
    font-size: 15px;
    color: #DB5D15;
    padding: 0 0 0 5px;
    font-weight: 500;
}
.home-card-title{
    font-size: 16px;
    color: #000000;
    padding: 0 0 0 5px;
    font-weight: 500;
}
.home-card-calender-icon{
    width: 15px;
    height: 15px;
    float: left;
    margin: 0 0 10px 5px;
}
.home-card-calender{
    float: left;
    font-size: 13px;
    color: #000000;
    line-height: 15px;
    margin: 0 0 0 10px;
}