.admin-guide-list-title{
    height: 40px;
    border-left: 4px solid #000000;
    font-size: 20px;
    line-height: 40px;
    padding: 0 0 0 30px;
}
.admin-guide-list-search-icon{
    width: 25px;
    height: 25px;
    float: right;
    cursor: pointer;
    margin: 12px 10px 22px 0;
}
.admin-guide-list-search-input-group{
    width: 200px;
    float: right;
    margin: 5px 20px 0 0;
}
.admin-guide-list-search-append{
    background-color: #ffffff;
    border-left: none;
    border-radius: 0 5px 5px 0 !important;
    cursor: pointer;
}
.admin-guide-list-search-input{
    border-right: none;
}
.admin-guide-list-search-input:focus{
    box-shadow: none;
    border-top: 1px solid #CED4DA;
    border-bottom: 1px solid #CED4DA;
    border-left: 1px solid #CED4DA;
}
.admin-guide-list-add-blog-btn{
    background-color: #07B5B6;
    color: #ffffff;
    float: right;
    margin: 5px 20px 0 0;
}
.admin-guide-list-add-blog-btn:hover{
    color: #ffffff;
}
.admin-guide-list-add-blog-btn:focus{
    box-shadow: none;
}
.admin-guide-list-add-blog-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 10px 0 0;
}
.admin-guide-list-table-option-box{
    width: 30px;
    height: 30px;
    /* border: 1px solid #CED4DA; */
    cursor: pointer;
    border-radius: 50%;
    margin: 0 auto;
}

.dropdown-item:active{
  background-color:#f8f9fa !important;
}