/* Create Post */
.hackable-post-body-create-post-bg{
    border: none;
    width: 100%;
}
.hackable-post-body-create-post-title{
    background-color: #E9EBEE;
    font-size: 25px;
    font-weight: 400;
}
.hackable-post-body-create-post-input{
    border: none;
}
.hackable-post-body-create-post-input:focus{
    box-shadow: none;
}
.hackable-post-body-create-post-btn{
    background-color: #4267B2;
    width: 95%;
    color: #ffffff;
    font-weight: 500;
}
.hackable-post-body-create-post-btn:hover{
    color: #ffffff;
    background-color: rgb(40, 86, 179);
}
.hackable-post-body-create-post-btn:focus{
    box-shadow: none;
}

/* Post Card */
.hackable-post-body-post-card-bg{
    background-color: #ffffff;
    border: none;
    width: 100%;
}
.hackable-post-body-post-card-img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid #808080;
    margin: 5px 0 0 0;
}
.hackable-post-body-post-card-title{
    font-size: 25px;
    font-weight: 400;
}
.hackable-post-body-post-card-comment-img{
    width: 25px;
    height: 25px;
    margin: 0 20px 0 0;
}
.hackable-post-body-post-card-comment-input{
    height: 40px;
    width: 100%;
    border: 1px solid #ced4da;
    border-radius: 20px;
    background-color: #F9F9F9;
}
.hackable-post-body-post-card-comment-input:focus{
    box-shadow: none;
    border: 1px solid #ced4da;
}
.hackable-post-body-post-card-comment-title{
    color: blue;
    font-size: 25px;
    font-weight: 400;
}