/* Progress bar */
.guide-description-body-bg{
    overflow-y: scroll;
    overflow-x: hidden;
}
.guide-description-body-progress-bar-bg{
    background-color: #DB5D15;
    text-align: center;
    color: #FDF500;
    font-size: 20px;
    height: 50px;
    line-height: 50px;
}
.guide-description-body-progress-bar-container{
    background-color: #ffffff;
    border-radius: 10px;
    height: 20px;
    margin: 15px 0 0 0;
}
.guide-description-body-progress-bar{
    background-color: #FDF500;
    border-radius: 10px;
    height: 20px;
}

/* Sidebar */
.guide-description-body-sidebar-bg{
    background-color: #F0F0F0;
    height: 100%;
}
.guide-description-body-sidebar-title{
    text-align: center;
    font-size: 25px;
    font-weight: 500;
    margin: 30px 0 0 0;
}
.guide-description-body-sidebar-update-interval{
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    margin: 15px 0 15px 0;
}
.guide-description-body-sidebar-option{
    /* color: #000000; */
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    margin: 15px 0 15px 0;
    cursor: pointer;
}
.guide-description-body-sidebar-option-active{
    color: #008F21;
}

/* Description */
.guide-description-body-description-title{
    text-align: left;
    font-size: 25px;
    font-weight: 500;
}
.guide-description-body-description-title-active{
    color: #008F21;
}
.guide-description-body-description-text{
    text-align: left;
    font-size: 17px;
    font-weight: 500;
}
.guide-description-body-description-img{
    width: 100%;
    border-radius: 5px;
}
.guide-description-body-description-resource-container{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}
.guide-description-body-description-resource-box{
    width: 400px;
    height: 400px;
    border-radius: 10px;
    margin: 20px auto;
    border: 1px solid #F3F3F3;
}
.guide-description-body-description-resource-img{
    width: 100%;
    height: 200px;
    margin: 15px 0 0 0;
    cursor: pointer;
}
.guide-description-body-description-resource-img:hover{
    width: 110%;
    height: 210px;
    z-index: -100;
}
.guide-description-body-description-resource-title{
    color: #28A745;
    text-align: left;
    font-size: 13px;
    font-weight: 500;
}
.guide-description-body-description-resource-text{
    color: #6C757D;
    text-align: left;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
}
.guide-description-body-description-resource-date{
    color: #000000;
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 30px;
}
.guide-description-body-description-resource-info-text{
    color: #000000;
    float: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 15px;
}
.guide-description-body-description-resource-info-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 0 0 15px;
}
.guide-description-body-description-resource-btn{
    background-color: #DB5D15;
    height: 30px;
    line-height: 10px;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 500;
}
.guide-description-body-description-resource-btn:hover{
    color: #ffffff;
}
.guide-description-body-description-resource-btn:focus{
    box-shadow: none;
}