.cursor-pointer{
    cursor: pointer;
}
.flex-container{
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    padding: 15px;
    justify-content: space-between;
}
.view-body-bg{
    background-color: #F6F6F6;
    height: 100%;
    min-height: 100%;
}
.view-body-scroll{
    overflow-y: scroll;
    height: 100%;
    width: 100%;
    /* background-color: pink; */
}

/* Blog Avtion */

.blog-action-view-bg{
    overflow: hidden;
    height: 100vh;
    width: 100vw;
    background-color: #0A0A0A;
}
.blog-action-view-blue-bg{
    height: 50vh;
    min-height: 50vh;
    background-color: #07B5B6;
    border-radius: 30px 0 0 0;
}
.blog-action-view-sidebar-open{
    float: left;
    width: 20%;
    height: 100vh;
}
.blog-action-view-body-open{
    float: left;
    width: 80%;
    height: 100vh;
    background-color: #ffffff;
    border-radius: 30px 0 0 30px;
}
.blog-action-view-sidebar-close{
    float: left;
    width: 5%;
    height: 100vh;
}
.blog-action-view-body-close{
    float: left;
    width: 95%;
    height: 100vh;
    background-color: #ffffff;
    border-radius: 30px 0 0 30px;
}

@media screen and (max-width:1300px){
    .blog-action-view-sidebar-close{
        width: 7%;
    }
    .blog-action-view-body-close{
        width: 93%;
    }
}

@media screen and (max-width:1200px){
    .blog-action-view-sidebar-close{
        width: 8%;
    }
    .blog-action-view-body-close{
        width: 92%;
    }
}

@media screen and (max-width:1100px){
    .blog-action-view-sidebar-close{
        width: 9%;
    }
    .blog-action-view-body-close{
        width: 91%;
    }
}

@media screen and (max-width:1000px){
    .blog-action-view-sidebar-close{
        width: 10%;
    }
    .blog-action-view-body-close{
        width: 90%;
    }
}

@media screen and (max-width:900px){
    .blog-action-view-sidebar-close{
        width: 11%;
    }
    .blog-action-view-body-close{
        width: 89%;
    }
}

@media screen and (max-width:800px){
    .blog-action-view-sidebar-close{
        width: 12%;
    }
    .blog-action-view-body-close{
        width: 88%;
    }
}

@media screen and (max-width:700px){
    .blog-action-view-sidebar-close{
        width: 13%;
    }
    .blog-action-view-body-close{
        width: 87%;
    }
}

@media screen and (max-width:600px){
    .blog-action-view-sidebar-close{
        width: 14%;
    }
    .blog-action-view-body-close{
