    }
}

@media screen and (max-width:767px){
    .blog-action-view-sidebar-close{
        float: left;
        width: 15%;
        height: 100vh;
    }
    .blog-action-view-body-close{
        float: left;
        width: 85%;
        height: 100vh;
        background-color: #ffffff;
        border-radius: 30px 0 0 30px;
    }
    .admin-dashboard-margin{
        margin-top: 20px;
    }
}