.sign-up-main-container{
  background-color: #F4F5F8;
  text-align: center;
}
.sign-up-container{
  border-radius:25px;
  padding:40px 20px 40px 20px;
  box-shadow: 0px 0px 2px 0px #F4F5F8;
  /* height: 90vh !important; */
  height: 87% !important;
}

.Sign-up-heading{
    text-align: left;
    font-size: 40px;
    font-weight: 700;
    margin-top: 12px;
}
.input-container {
    padding-bottom: 1em;
    text-align: left;
    margin-top: 20px;
    width: 100%;
}
.left-inner-addon {
    position: relative;
}
.left-inner-addon input {
    padding-left: 55px !important; 
    padding-bottom: 10px;
    border-left: none;
    border-right: none;
    color: #979797;
    border-top: none;
}
.input-filed-border-color{
  border-bottom:1px solid #B1B1B1;
}
.Sign-up-input-fields{
    width: 90%;
    font-size: 14px;
    color: #979797;
}
.Sign-up-input-fields:focus{
    outline: none;
}
.left-inner-addon img {
    position: absolute;
    pointer-events: none;
    width: 16px;
    margin-top: 5px;
    margin-left: 7px;
}


  /* Hide the browser's default checkbox */
  .login-page-checkbox-container input {
    opacity: 0;
    cursor: pointer;
    position: absolute;
  }
  
  /* Create a custom checkbox */
  .new-created-check-box {
    position: absolute;
    box-shadow: 0px 0px 5px #F4F5F8;
    border-radius: 2px;
    top: 6px;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: white;
    margin-left: 30px;
    border: 1px solid #BABABA;
  }
  /* When the checkbox is checked, add a background color if you want */
  .login-page-checkbox-container input:checked ~ .new-created-check-box {
    background-color: white;
  }
  
  /* Create the new-created-check-box/indicator (hidden when not checked) */
  .new-created-check-box:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the new-created-check-box when checked */
  .login-page-checkbox-container input:checked ~ .new-created-check-box:after {
    display: block;
  }
  
  /* Style the new-created-check-box/indicator */
  .login-page-checkbox-container .new-created-check-box:after {
    left: 4px;
    top: 2px;
    width: 5px;
    height: 8px;
    border: solid black;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
.Sign-up-agree-text{
    margin-left: 40px;
    font-size: 11px;
    font-weight: 600;
}
.term-and-service-text{
    border-bottom: 1px solid black;
    cursor: pointer;
}
.sign-up-Register-button{
    background-color: #574B90;
    color: white;
    border-radius: 5px;
    border: none;
    width: 130px;
    height: 40px;
    font-size: 14px;
    text-align: center;
    margin-top: 20px;
    margin-left: 10px;
}
.sign-up-Register-button:focus{
    outline: none;
}
.Already-have-an-account{
    color: black;
    text-decoration: none;
    font-size: 11px;
    font-weight: 600;
    cursor: pointer;
  
}
.Already-have-an-account:hover{
    color: black;
    text-decoration: none;
}
.Already-have-an-account-show-after-hide-image{
  display: none;
}

.sign-up-profile-img{
  float: left;
  height: 100px;
  width: 100px;
  border: 1px solid;
  border-radius: 50%;
}

.sign-up-profile-img-btn{
  margin: 5% 0 0 2%;
  border: 1px solid;
}

@media only screen and (max-width:1100px){
  .sign-up-container{
    height: 88% !important;
  }
}
@media only screen and (max-width:991px){
  .sign-up-col-image{
    display: none
  }
  .Already-have-an-account-show-after-hide-image{
    display: inline;
    color: black;
    text-decoration: none;
    font-size: 11px;
    font-weight: 600;
  }
  .sign-up-container{
    height: 91% !important;
  }
}
@media only screen and (max-width:767px){
  .sign-up-container{
    height: 95% !important;
  }
}