.admin-nav-bg{
    background-color: #07B5B6;
}
.admin-nav-menu-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 0 0 10px;
    cursor: pointer;
}
.admin-nav-title{
    font-size: 30px;
    line-height: 25px;
    font-weight: 600;
    color: #ffffff;
    float: left;
    margin: 0 0 0 30px;
}
.admin-nav-home-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 0 0 20px;
}
.admin-nav-sub-title{
    font-size: 15px;
    line-height: 30px;
    font-weight: 400;
    color: #ffffff;
    float: left;
    margin: 0 0 0 30px;
}
.admin-nav-action-container{
    width: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    float: right;
}
.admin-nav-action-box{
    width: 30px;
    height: 30px;
    margin: 0 10px;
    cursor: pointer;
}

@media screen and (max-width:767px){
    .admin-nav-menu-icon{
        width: 20px;
        height: 20px;
        margin: 5px 0 0 10px;
    }
    .admin-nav-title{
        font-size: 20px;
    }
    .admin-nav-home-icon{
        width: 15px;
        height: 15px;
        margin: 7px 0 0 20px;
    }
    .admin-nav-sub-title{
        font-size: 10px;
        line-height: 30px;
        font-weight: 400;
        color: #ffffff;
        float: left;
        margin: 0 0 0 5px;
    }
}