.blog-view-bg {
  background-color: #f6f6f6;
  padding: 60px 0 0 0;
}
.blog-view-card-container {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.blog-view-title-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  background-color: #f0f0f0;
}
.blog-view-title-box {
  text-align: center;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  font-weight: 500;
  padding: 0 30px;
  cursor: pointer;
}
.blog-view-title-box-active {
  text-align: center;
  height: 40px;
  line-height: 40px;
  background-color: #f0f0f0;
  font-size: 20px;
  font-weight: 500;
  padding: 0 30px;
  color: #db5d15;
  cursor: pointer;
}

