.home-view-load-btn {
  border-radius: 30px;
  border: 2px solid #db5d15;
  color: #db5d15;
  font-size: 15px;
  font-weight: 500;
}
.home-view-load-btn:hover {
  background-color: #db5d15;
  color: #ffffff;
}
.home-view-load-btn:focus {
  box-shadow: none;
}
@media screen and (min-width: 767px) {
  .home-view-tag-box {
    position: absolute;
    bottom: 0;
    padding: 0 15px 0 0;
  }
}
@media screen and (max-width: 767px) {
  .home-view-mail-box {
    margin: 20px 0 0 0;
  }
  .home-view-tag-box {
    margin: 20px 0 0 0;
  }
}
