.admin-category-list-title{
    height: 40px;
    border-left: 4px solid #000000;
    font-size: 20px;
    line-height: 40px;
    padding: 0 0 0 30px;
}
.admin-category-list-search-icon{
    width: 25px;
    height: 25px;
    float: right;
    cursor: pointer;
    margin: 12px 10px 22px 0;
}
.admin-category-list-search-input-group{
    width: 200px;
    float: right;
    margin: 5px 20px 0 0;
}
.admin-category-list-search-append{
    background-color: #ffffff;
    border-left: none;
    border-radius: 0 5px 5px 0 !important;
    cursor: pointer;
}
.admin-category-list-search-input{
    border-right: none;
}
.admin-category-list-search-input:focus{
    box-shadow: none;
    border-top: 1px solid #CED4DA;
    border-bottom: 1px solid #CED4DA;
    border-left: 1px solid #CED4DA;
}
.admin-category-list-add-blog-btn{
    background-color: #07B5B6;
    color: #ffffff;
    float: right;
    margin: 5px 20px 0 0;
}
.admin-category-list-add-blog-btn:hover{
    color: #ffffff;
}
.admin-category-list-add-blog-btn:focus{
    box-shadow: none;
}
.admin-category-list-add-blog-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 10px 0 0;
}
.admin-category-list-table-option-box{
    width: 30px;
    height: 30px;
    /* border: 1px solid #CED4DA; */
    cursor: pointer;
    border-radius: 50%;
    margin: 0 auto;
}
.admin-category-list-table-action-box{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #07B5B6;
    /* float: right; */
    margin: auto;
    cursor: pointer;
}
.admin-category-list-table-action-edit-icon{
    width: 20px;
    height: 20px;
    margin: 7px 0 0 3px;
}

.admin-category-list-modal-close{
    width: 20px;
    height: 20px;
    background-color: red;
    color: #ffffff;
    text-align: center;
    line-height: 20px;
    border-radius: 50%;
    position: absolute;
    right: -10px;
    top: -10px;
    cursor: pointer;
}
.admin-category-list-modal-add-btn{
    width: 150px;
    background-color: #07B5B6;
    margin: auto;
    color: #ffffff;
}
.admin-category-list-modal-add-btn:hover{
    color: #ffffff;
}
.admin-category-list-modal-add-btn:focus{
    box-shadow: none;
}
.admin-category-list-modal-input-icon{
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;
    background-color: #ffffff;
}
.admin-category-list-modal-input{
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;
}
.admin-category-list-modal-input:focus{
    box-shadow: none;
    border-bottom: 1px solid #ced4da;
}

.dropdown-item:active{
  background-color:#f8f9fa !important;
}