/* Banner */
.guide-body-banner-bg{
    background-image: url(../../Assets/Images/Guide/guide.png);
    background-size: cover;
    width: 100%;
    height: 250px;
}
.guide-body-banner-title{
    font-size: 50px;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
}
.guide-body-banner-text{
    font-size: 25px;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
}

/* Card */
.guide-body-card-box{
    width: 600px;
    height: 400px;
    border-radius: 10px;
    margin: 20px auto;
    border: 1px solid #F3F3F3;
}
.guide-body-card-img{
    width: 100%;
    height: 200px;
    margin: 15px 0 0 0;
    cursor: pointer;
}
.guide-body-card-title{
    color: #28A745;
    text-align: left;
    font-size: 13px;
    font-weight: 500;
}
.guide-body-card-text{
    color: #6C757D;
    text-align: left;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
}
.guide-body-card-date{
    color: #000000;
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 30px;
}
.guide-body-card-info-text{
    color: #000000;
    float: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 15px;
}
.guide-body-card-info-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 0 0 15px;
}
.guide-body-card-btn{
    background-color: #DB5D15;
    height: 30px;
    line-height: 10px;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 500;
}
.guide-body-card-btn:hover{
    color: #ffffff;
}
.guide-body-card-btn:focus{
    box-shadow: none;
}

@media screen and (max-width:600px){
    .guide-body-card-box{
        width: 300px;
    }
}