/* Banner */
.blog-body-banner-bg{
    background-image: url(../../Assets/Images/Blog/blog.png);
    background-size: cover;
    width: 100%;
    height: 250px;
}
.blog-body-banner-title{
    font-size: 60px;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
    margin: 6% 0 0 0;
}

/* Card */
.blog-card-box{
    width: 400px;
    height: 400px;
    border-radius: 10px;
    margin: 20px auto;
    border: 1px solid #F3F3F3;
    background-color: #ffffff;
}
.blog-card-img{
    width: 100%;
    height: 200px;
    margin: 15px 0 0 0;
    cursor: pointer;
}
.blog-card-title{
    color: #28A745;
    text-align: left;
    font-size: 13px;
    font-weight: 500;
}
.blog-card-text{
    color: #6C757D;
    text-align: left;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
}
.blog-card-date{
    color: #000000;
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 30px;
}
.blog-card-info-text{
    color: #000000;
    float: left;
    font-size: 15px;
    font-weight: 500;
    padding: 0 0 0 15px;
}
.blog-card-info-icon{
    width: 25px;
    height: 25px;
    float: left;
    margin: 0 0 0 15px;
}
.blog-card-btn{
    background-color: #DB5D15;
    height: 30px;
    line-height: 10px;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 500;
}
.blog-card-btn:hover{
    color: #ffffff;
}
.blog-card-btn:focus{
    box-shadow: none;
}