.sign-in-main-container{
    background-color: #F4F5F8;
    text-align: center;
    width: 100%;
    border-radius: 100px;
  }
.sign-in-container{
    border-radius: 25px;
    box-shadow: 0px 0px 2px 0px #F4F5F8;
    height: 90vh !important;
  }
.Sign-In-heading{
    text-align: left;
    font-size: 40px;
    font-weight: 700;
}
.sign-in-input-container {
    padding-bottom: 1em;
    text-align: left;
    margin-top: 20px;
    width: 100%;
}
.Sign-in-input-container-position {
    position: relative;
}
.Sign-in-input-container-position input {
    padding-left: 55px !important; 
    padding-bottom: 10px;
    border-left: none;
    border-right: none;
    border-top: none;
}
.Sign-IN-input-fields{
    width: 70%;
    font-size: 14px;
}

.Image-colomn-padding{
  padding-top: 50px;
}
.Sign-IN-input-fields:focus{
    outline: none;
}
.Sign-in-input-container-position img {
    position: absolute;
    pointer-events: none;
    width: 16px;
    margin-top: 5px;
    margin-left: 8px;
}

/* Hide the browser's default checkbox */
.sign-in-page-checkbox-container input {
    opacity: 0;
    cursor: pointer;
    float: left;
    position: absolute;

  }
  .sign-in-page-checkbox-container{
      float: left;
  }
  
  /* Create a custom checkbox */
  .new-created-check-box-sign {
    position: absolute;
    border-radius: 3px;
    top: 8px;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: white;
    margin-left: 18px;
    border: 1px solid #BABABA;
  }
  /* When the checkbox is checked, add a background color if you want */
  .sign-in-page-checkbox-container input:checked ~ .new-created-check-box-sign {
    background-color: white;
  }
  
  /* Create the new-created-check-box/indicator (hidden when not checked) */
  .new-created-check-box-sign:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the new-created-check-box when checked */
  .sign-in-page-checkbox-container input:checked ~ .new-created-check-box-sign:after {
    display: block;
  }
  
  /* Style the new-created-check-box/indicator */
  .sign-in-page-checkbox-container .new-created-check-box-sign:after {
    left: 4px;
    top: 1px;
    width: 5px;
    height: 10px;
    border: solid black;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .sign-in-Register-button{
    background-color: #574B90;
    color: white;
    border-radius: 5px;
    border: none;
    width: 120px;
    height: 35px;
    font-size: 12px;
    font-weight: bold;
    text-align: center;
    margin: 20px 10px;
}
.sign-in-Register-button:focus{
    outline: none;
}
.Sign-in-keep-text{
    font-size: 10px;
    margin-left: 30px;
    font-weight: 600;
}
.not-have-an-account-text{
    color: black;
    text-decoration: none;
    font-size: 11px;
    font-weight: 500;
    cursor: pointer;
}
.not-have-an-account-text:hover{
    color: black;
    text-decoration: none;
}

.sign-in-cover-image{
  margin: 0 0 0 30%;
}
@media only screen and (max-width:1010px){
    .Sign-IN-input-fields{
        width: 100%;
    }
}

@media only screen and (max-width:1007px){
  .Image-colomn-padding{
    display: none
  }
}