.card-padding-div{
    padding: 5% 33% 11% 32% ;
}
@media only screen and (max-width:1150px){
    .card-padding-div{
        animation: Pading 1s ease-in-out;
        padding: 5% 30% 11% 30% ;
    }
}
@keyframes Pading{
    0%{
        padding: 10% 35% 11% 35% ;

    }
    100%{
        padding: 10% 30% 11% 30% ;
    }
}
@media only screen and (max-width:840px){
    .card-padding-div{
        padding: 10% 25% 11% 25% ;
        animation: Pading-at-800-screen-view 1s ease;
    }
}
@keyframes Pading-at-800-screen-view{
    0%{
        padding: 10% 30% 11% 30% ;

    }
    100%{
        padding: 10% 25% 11% 25% ;
    }
}
@media only screen and (max-width:700px){
    .card-padding-div{
        padding: 14% 10% 10% 10% ;
        animation: Pading-at-700-screen-view 1s ease;

    }
}
@keyframes Pading-at-700-screen-view{
    0%{
        padding: 10% 25% 11% 25% ;

    }
    100%{
        padding: 14% 10% 11% 10% ;
    }
}
@media only screen and (max-width:530px){
    .card-padding-div{
        padding: 15% 5% 5% 5% ;
        animation: Pading-at-530-screen-view 1s ease;
    }
}
@keyframes Pading-at-530-screen-view{
    0%{
        padding: 14% 10% 10% 10% ;

    }
    100%{
        padding: 15% 5% 5% 5% ;
    }
}

/* The container-login-page-checkbox */
.login-page-checkbox-container {
    position: relative;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 12px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 45%;
  }

  .Login-Remember-me-text{
      float: left;
      margin-left: 38px;
      margin-top: 3px;
  }
  /* Hide the browser's default checkbox */
  .login-page-checkbox-container input {
    opacity: 0;
    cursor: pointer;
    float: left;
    position: absolute;

  }
  
  /* Create a custom checkbox */
  .new-created-check-box {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    border-radius: 3px;
    background-color: white;
    /* box-shadow: 0 8px 20px 0 #E9ECEE, 0 8px 20px 0 #E9ECEE; */
    box-shadow: 2px 2px 4px 0px rgb(158, 157, 157);
  }
  /* When the checkbox is checked, add a background color if you want */
  .login-page-checkbox-container input:checked ~ .new-created-check-box {
    background-color: white;
  }
  
  /* Create the new-created-check-box/indicator (hidden when not checked) */
  .new-created-check-box:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the new-created-check-box when checked */
  .login-page-checkbox-container input:checked ~ .new-created-check-box:after {
    display: block;
  }
  
  /* Style the new-created-check-box/indicator */
  .login-page-checkbox-container .new-created-check-box:after {
    left: 10px;
    top: 7px;
    width: 5px;
    height: 10px;
    border: solid black;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }