.admin-sidebar-option{
    float: right;
    width: 90%;
    color: #ffffff;
    font-size: 15px;
    cursor: pointer;
    height: 50px;
    line-height: 50px;
}
.admin-sidebar-option-active{
    float: right;
    width: 90%;
    color: #ffffff;
    font-size: 15px;
    cursor: pointer;
    height: 50px;
    line-height: 50px;
    background-color: #3B3B3B;
    border-radius: 30px 0 0 30px;
}
.admin-sidebar-option-icon{
    width: 30px;
    height: 30px;
    float: left;
    margin: 10px 10px 0 20px;
}

@media screen and (max-width:1100px){
    .admin-sidebar-option-text{
        display: none;
    }
}