.side-option-container{
    height: 200px;
    width: 70px;
    background-color: #ffffff;
    position: fixed;
    top: 40%;
    left: 0;
    border-radius: 0 20px 20px 0;
    z-index: 100;
    background-color: #ffffff;
    transition: width .3s;
}
.side-option-container:hover{
    width: 100px;
    background-color: #F38D53;
}
.side-option-text{
    color: #ffffff;
    text-align: center;
    line-height: 50px;
    margin: -10px 0 -20px 0;
    position: absolute;
    display: none;
    width: 100%;
}
.side-option-container:hover .side-option-text{
    display: block;
}
.side-option-icon-circle{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border: 1px solid #DB5D15;
    text-align: center;
    margin: 25px auto;
    background-color: #ffffff;
    cursor: pointer;
}
.side-option-icon-img{
    width: 20px;
    height: 20px;
    margin: 5px 0 0 0;
}